import React from "react";

const Banner = () =>{
    return(
        <div>
        <img className={'phone-wallpaper-copy'} src={require('../assets/phone_wallpaper_copy@4x.png').default} />
        <div className={'links-container'}>
                <p>Music</p>
                <p>Videos</p>
                <p>Buy</p>
                <p>Discography</p>
            </div>
        <div style={{width:'100%',display:'flex',justifyContent:'center',position:"absolute",bottom:'30px'}}>
            <div className={'perrick'}>PERRICK</div>
        </div>
        </div>
    )
}
export default Banner